<template>
    <Modal title="图片查看器" v-model="show">
        <img :src="src" width="100%">
        <div slot="footer">
            <Button type="primary" @click="show = false">关闭</Button>
        </div>
    </Modal>
</template>

<script>
export default {
    props:{
        is_show:{
            type:Boolean,
            default:false,
        },
        src:{
            type:String,
            default:'',
        },
    },
    data() {
        return {
            show:false,
        }
    },

    watch: {
        is_show(val) {
            if (val)  {
                this.show = true;
                this.$emit('closePicture');
            }
        }
    },
    
}
</script>
